<template>
  <v-container>
    <v-layout text-xs-center wrap>
      <v-flex mb-4 xs12>
        <h1>{{greeting}}</h1>
      </v-flex>
      <v-flex mb-4 xs12>
        <logo/>
      </v-flex>
      <v-flex xs12>
        <v-layout align-space-between justify-center row reverse fill-height>
          <v-flex xs4 class="d-flex ma-2">
            <progress-card heading="Space used" current="60" to="300" unit="GB"/>
          </v-flex>
          <v-flex xs4 class="d-flex ma-2">
            <progress-card
              heading="Current backup job progress"
              :current="value"
              to="100"
              unit="%"
            />
          </v-flex>
          <v-flex xs4 class="d-flex ma-2">
            <chart-card height="100"/>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-flex xs12>
        <v-layout align-space-between justify-center row reverse fill-height>
          <v-flex xs4 class="d-flex ma-2">
            <v-card>
              <v-card-title>Total files backed up</v-card-title>
              <v-card-text>487954</v-card-text>
            </v-card>
          </v-flex>
          <v-flex xs4 class="d-flex ma-2">
            <v-card>
              <v-card-title>Next backup job</v-card-title>
              <v-card-text>Weekly job 1 (in 4 days)</v-card-text>
            </v-card>
          </v-flex>
          <v-flex xs4 class="d-flex ma-2">
            <v-card>
              <v-card-title>Files backed up this month</v-card-title>
              <v-card-text>4567</v-card-text>
            </v-card>
          </v-flex>
          <v-flex xs4 class="d-flex ma-2">
            <v-card>
              <v-card-title>Previous backup job</v-card-title>
              <v-card-text>Weekly job (3 days ago)</v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-flex>
      <!-- <v-flex>todo: bucket stats( created, name, type, snapshots stored, current size, current number of files)</v-flex> -->
    </v-layout>
  </v-container>
</template>

<script>
import Logo from "../misc/Logo";
import ProgressCard from "./ProgressCard";

import ChartCard from "./ChartCard";
export default {
  components: {
    ChartCard,
    ProgressCard,
    Logo
  },
  data: () => ({
    interval: {},
    value: 0
  }),
  beforeDestroy() {
    clearTimeout(this.interval);
  },
  mounted() {
    this.interval = setTimeout(this.nextTimeout, 100);
  },
  methods: {
    nextTimeout() {
      if (this.value >= 100) {
        this.value = 0;
      }
      this.value += Math.floor(Math.random() * 30);
      this.value = Math.min(100, this.value);
      this.interval = setTimeout(this.nextTimeout, Math.random() * 500 + 500);
    }
  },
  computed: {
    greeting() {
      return this.$t("home.greeting", {
        name:
          this.$store.getters["settings/user"].name ||
          this.$store.getters["auth/username"]
      });
    }
  }
};
</script>

